import { Component, cloneElement } from "react";

import isSameDay from "date-fns/is_same_day";
import isWeekend from "date-fns/is_weekend";

import Head from "./Head";
import Calendar from "./Calendar";
import $ from "./style.css";

const specialDates = [
	{
		type: "holiday",
		date: "2018-05-21",
		name: "elena",
	},
	{
		type: "holiday",
		date: "2018-06-21",
		name: "test",
	},
	{
		type: "event",
		date: "2018-05-29",
		name: "event",
	},
	{
		type: "event",
		date: "2018-05-21",
		name: "event",
	},
];

const SpecialDay = ({ date }) => {
	specialDates.map((specialDate, key) => {
		if (isSameDay(date, new Date(specialDate.date))) {
			console.log("test");
			return (
				<div
					key={key}
					style={{
						width: "100%",
						border: `2px solid ${
							specialDate.type == "holiday" ? "red" : "green"
						}`,
					}}
				/>
			);
		}
	});
};

function onRender({ dayNumber, date }, cell) {
	const content =
		dayNumber === 12 ? (
			<div>dev</div>
		) : (
			<div>
				{dayNumber}
				<SpecialDay date={date} />
			</div>
		);

	const isWeekendDay = isWeekend(date);

	const styles = {
		backgroundColor: isWeekendDay ? "rgba(0,0,0,0.1)" : "",
		// color: "green",
	};

	if (cell != null)
		return cloneElement(
			cell,
			{
				style: styles,
			},
			content
		);
	return content;
}

const Selected = ({ date, dayNumber }, cell) => {
	const styles = {
		borderColor: "yellow",
	};
	if (cell != null)
		return cloneElement(
			cell,
			{
				style: styles,
			},
			dayNumber
		);
	return content;
};

export default () => {
	return (
		<div>
			<Head />
			<Calendar
				date="2018-05-21"
				onRender={onRender}
				selected={Selected}
			/>
		</div>
	);
};



